@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.6/dist/web/static/pretendard.css");
.allWrap {
    width: 100%;
    margin: 40px auto;
    font-family: 'Pretendard';
    letter-spacing: -0.05em;
    text-align: center;
}
.customSelect {
    position: relative;
    width: 60%;
    max-width: 360px;
    height: 40px;
    margin: 0 auto;
    border: 2px solid #3b76c5;
    border-radius: 20px;
    padding: 9px 18px;
    box-sizing: border-box;
    font-size: 15px;
    color: #3b76c5;
    text-align: left;
    background: url('../public/images/selectArrow.png') no-repeat 95% center #fff;
    z-index: 1;
    cursor: pointer;
}
.ulWrap {
    display: flex;
    justify-content: center;
}
.typeSelect {
    list-style-type: none;
    position: absolute;
    width: 60%;
    max-width: 360px;
    height: 141px;
    border: solid 1px #cad9e9;
    border-radius: 0 0 20px 20px;
    background-color: #f2f8ff;
    text-align: left;
    margin-top: -20px;
    /* margin: -20px 20% 0 20%; */
    padding: 19px 0 0 0;
    box-sizing: border-box;
}
.typeList {
    height: 40px;
    line-height: 40px;
    padding-left: 15px;
    font-size: 14px;
    cursor: pointer;
    box-sizing: border-box;
}
.typeList:last-child {
    border-radius: 0 0 20px 20px;
}
.typeList:hover {
    background-color: #e3f1ff;
}
.contWrap {
    width: 90%;
    max-width: 420px;
    padding: 35px;
    margin: 20px auto;
    background-color: #f7f7f7;
    border-radius: 15px 15px 0 0;
    box-sizing: border-box;
}
.lengthInput {
    background: url('../public/images/length.png') no-repeat left center / auto 100%;
}
.temperatureInput {
    background: url('../public/images/temperature.png') no-repeat left center / auto 100%;
}
.massInput {
    background: url('../public/images/mass.png') no-repeat left center / auto 100%;
}
.lengthInput, .temperatureInput, .massInput {
    padding-left: 70px;
    text-align: left;
}
.lengthInputWrap:nth-child(1), .temperatureInputWrap:nth-child(1), .massInputWrap:nth-child(1) {
    background-position: left -70%;
    padding-bottom: 40px;
    margin-bottom: 40px;
    border-bottom: 1px dotted #c2c2c2;
}
.lengthInputWrap:nth-child(2), .temperatureInputWrap:nth-child(2), .massInputWrap:nth-child(2) {
    margin-bottom: 10px;
}
.infoMessage {
    font-size: 14px;
    margin: 0 0 8px 0;
}
input {
    width: 72%;
    height: 30px;
    border: 1px solid #ebebeb;
    border-radius: 0;
    padding-left: 10px;
    margin-right: 1%;
    box-sizing: border-box;
}
select {
    width: 27%;
    height: 30px;
    border: 1px solid #ebebeb;
    border-radius: 0;
    box-sizing: border-box;
}
.messageWrap {
    width: 90%;
    max-width: 420px;
    padding: 30px;
    margin: -20px auto 0 auto;
    background-color: #eaeaea;
    border-radius: 0 0 15px 15px;
    box-sizing: border-box;
}
.messageWrap span {
    background-color: #368ce3;
    color: #fff;
    padding: 4px 14px;
    border-radius: 4px;
    box-sizing: border-box;
    font-size: 14px;
    margin-right: 8px;
}